import React, {useState} from "react";
import PropTypes from 'prop-types';
import classNames from "classnames";
const PizzaBlock=({id,imageUrl,name, price,types,sizes,onAddToCart,cartItems})=>{
    const typeOfSizes=["тонкое","традиционное"];
    const nameSizes=[26,30,40];
    const [activeType,setActiveType]=useState(types[0]);
    const [activeSize,setActiveSize]=useState(sizes[0]);
    const key=cartItems[`${id}${typeOfSizes[activeType]}${activeSize}`];
    const addedCount=key ? key.items.length:0;
    const onSelectType=(index)=>{
        setActiveType(index);
    }
    const onSelectSize=(index)=>{
        setActiveSize(index);
    }
    const onAddPizza=({id,name,imageUrl})=>{
        const element={
            id,
            name,
            imageUrl,
            price,
            size:activeSize,
            type:typeOfSizes[activeType]
        }
        onAddToCart(element);
    }
    return (
        <div className="pizza-block">
            <img
                className="pizza-block__image"
                src={imageUrl}
                alt="Pizza"
            />
            <h4 className="pizza-block__title">{name}</h4>
            <div className="pizza-block__selector">
                <ul>
                    {typeOfSizes.map((type,index)=><li key={type} onClick={()=>onSelectType(index)} className={classNames({
                        active:index==activeType,
                        disabled:!types.includes(index),
                        })} >{type}</li>
                    )}
                </ul>
                <ul>
                    {
                        nameSizes.map((size,index)=><li key={size}
                        onClick={()=>onSelectSize(size)}
                                                        className={classNames({
                            active:size==activeSize,
                            disabled:!sizes.includes(size),
                        })} >{size} см</li>)
                    }
                </ul>
            </div>
            <div className="pizza-block__bottom">
                <div className="pizza-block__price">от {price} руб</div>
                <div onClick={()=>onAddPizza({id,name,imageUrl,price})} className="button button--outline button--add">
                    <svg
                        width="12"
                        height="12"
                        viewBox="0 0 12 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                            fill="white"
                        />
                    </svg>
                    <span>Добавить</span>
                    <i>{addedCount ? addedCount:0}</i>
                </div>
            </div>
        </div>
    )
}
PizzaBlock.propTypes={
    name:PropTypes.string.isRequired,
    imageUrl:PropTypes.string.isRequired,
    price:PropTypes.number.isRequired,
    types:PropTypes.arrayOf(PropTypes.number).isRequired,
    sizes:PropTypes.arrayOf(PropTypes.number).isRequired,
    onAddToCart:PropTypes.func.isRequired,
    addedCount:PropTypes.oneOfType([PropTypes.number])
}
PizzaBlock.defaultProps={
 types:[],
    name:"-----",
    price:"???",
    sizes:[],
    imageUrl:"https://dodopizza.azureedge.net/static/Img/Products/f035c7f46c0844069722f2bb3ee9f113_584x584.jpeg\"",
    addedCount:0,
}
export default PizzaBlock